import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb, Button, Table, Popconfirm, message } from 'antd';
const { Content } = Layout;

export class List extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            columns: [{
                title: '广告名称',
                dataIndex: 'name',
                className: "column-width-15"
            }, {
                title: '广告类型',
                dataIndex: "type",
                className: "column-width-10",
                render: type => type == "1" ? "桌面广告" : "启动广告"
            }, {
                title: '广告图',
                dataIndex: "img",
                className: "column-width-15",
                render: img => <img src={img} style={{width: '100%'}}/>
            }, {
                title: '链接地址',
                className: "column-width-15",
                dataIndex: 'link'
            }, {
                title: '添加日期',
                className: "column-width-15",
                dataIndex: 'time'
            }, {
                title: '是否显示',
                className: "column-width-10",
                dataIndex: 'status',
                render: status => status == "0" ? "不显示" : "显示"
            }, {
                title: "操作",
                className: "column-width-20",
                render: data => <div style={{"textAlign": "center"}}>
                    <Button type="primary" onClick={this.goToAdvEdit.bind(this, data.key)} style={{"marginRight": "20px"}}>修改
                    </Button>
                    <Popconfirm placement="left" title="是否确认删除?" onConfirm={this.deleteAdv.bind(this, data.key)}
                                okText="确认" cancelText="取消">
                        <Button type="danger">删除</Button>
                    </Popconfirm>
                </div>
            }],
            dataSource: [],
            pagination: {
                current: 1,
                total: 0
            },
            loading: false
        };
        ['changePage'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        this.getInfoList(1);
    }

    getInfoList(currentPage) {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/adv/getInfoList",
            data: {
                    current: currentPage ? currentPage : 1
            },
            callback: function(data) {
                that.setState(
                    {
                        loading: false,
                        pagination: {
                            current: currentPage,
                            total: data.data.total
                        },
                        dataSource: data.data.list
                    }
                )
            }
        });
    }

    addAdv() {
        hashHistory.push("/adv/add")
    }

    goToAdvEdit(id) {
        hashHistory.push("/adv/edit/" + id)
    }

    deleteAdv(id) {
        let that = this;
        ajax({
            url: "/adv/deleteInfo",
            method: "post",
            data: {id: id},
            callback: function(data) {
                if(data.status == 200) {
                    message.success("删除成功", 1);
                    let newDataSource = that.state.dataSource.filter(item => { return item.key != id});
                    that.setState({dataSource: newDataSource});
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        });
    }

    changePage(pagination) {
        this.getInfoList(pagination.current)
    }

    render() {
        return (
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    <Button icon="plus" onClick={this.addAdv} style={{"marginBottom": "20px"}}>添加广告</Button>
                    <Table
                        bordered
                        dataSource={this.state.dataSource}
                        columns={this.state.columns}
                        pagination={this.state.pagination}
                        onChange={this.changePage}
                        loading={this.state.loading}
                    />
                </Content>
            </Layout>
        )
    }
}

module.exports = List;


